<template>
  <div>
    <b-card>
      <h1>Alerta de Pausas</h1>
      <b-icon icon="exclamation-triangle-fill" variant="warning"></b-icon>
      <Page class="flex-container">
        <b-form class="flex-container">
          <b-form-row v-for="(breakItem, index) in dataBreaks" :key="index">
            <b-form-group
              :label="`${breakItem.reason}`"
              class="col-md-auto flex-container"
            >
              <b-form-timepicker v-model="breakItem.time" size="md"> </b-form-timepicker>
            </b-form-group>
          </b-form-row>
        </b-form>
        <div class="center-button">
          <b-button @click="saveAllBreaks">Salvar</b-button>
        </div>
      </Page>
      <b-modal ref="successModalBreaks" title="Sucesso ao salvar" hide-footer>
        <p>Os dados foram salvos com sucesso!</p>
      </b-modal>
    </b-card>

    <b-card>
      <h1>Termômetro</h1>
      <Page class="flex-container">
        <b-form class="flex-container">
          <b-form-row v-for="(Item, index) in dataVelocity" :key="index">
            <b-form-group :label="`${Item.title}`" class="col-md-auto flex-container">
              <b-form-input
                type="number"
                v-model="Item.value"
                size="md"
                min="0"
                max="100"
              >
              </b-form-input>
            </b-form-group>
          </b-form-row>
        </b-form>
        <div class="center-button">
          <b-button @click="saveAllVelocity">Salvar</b-button>
        </div>
      </Page>
      <b-modal ref="successModalVelocity" title="Sucesso ao salvar" hide-footer>
        <p>Os dados foram salvos com sucesso!</p>
      </b-modal>
    </b-card>

    <b-card>
      <h1>Tempo de espera (s)</h1>
      <Page class="flex-container">
        <b-form class="flex-container">
          <b-form-row v-for="(item, index) in dataCallDuration" :key="index">
            <b-form-group
              :label="`${item.limitForType}`"
              class="col-md-auto flex-container"
            >
              <b-form-input type="number" v-model="item.limit" size="md"> </b-form-input>
            </b-form-group>
          </b-form-row>
        </b-form>
        <div class="center-button">
          <b-button @click="saveCallDuration">Salvar</b-button>
        </div>
      </Page>
      <b-modal ref="successModalCallDuration" title="Sucesso ao salvar" hide-footer>
        <p>Os dados foram salvos com sucesso!</p>
      </b-modal>
    </b-card>
  </div>
</template>

<script>
import Page from "@/components/Page";
import axios from "axios";
import { socket } from "@/socket";

export default {
  name: "break-alert",
  components: {
    Page,
  },
  metaInfo: {
    title: "Configuração",
  },
  data: () => ({
    dataBreaks: [],
    dataVelocity: [],
    dataVelocityOld: [],
    dataCallDuration: [],
  }),
  mounted() {
    this.$store.commit("setTitle", "Configuração");
    this.loadBreaks();
    this.loadVelocity();
    this.loadDataVelocityOld();
    this.loadCallDuration();
  },
  methods: {
    async loadCallDuration() {
      try {
        let origin = location.origin.replace(/:\d+$/, ":3333");

        const response = await axios.get(`${origin}/call-duration-config`);
        this.dataCallDuration = response.data.map((item) => ({
          ...item,
          limitForType: item.limitForType.replace("tempoEspera", ""),
        }));
      } catch (error) {
        console.error("Erro ao carregar dados:", error);
      }
    },

    async saveCallDuration() {
      try {
        let origin = location.origin.replace(/:\d+$/, ":3333");
        for (const item of this.dataCallDuration) {
          const { id, limitForType, limit } = item;
          const originalLimitForType = `tempoEspera${limitForType}`;
          await axios.put(`${origin}/call-duration-config/${id}`, {
            limit: Number(limit),
            limitForType: originalLimitForType,
          });
        }
        socket.emit("callDuration", "send call duration");
        this.$refs.successModalCallDuration.show();
      } catch (error) {
        console.error("Erro ao salvar dados:", error);
      }
    },

    async loadVelocity() {
      try {
        let origin = location.origin.replace(/:\d+$/, ":3333");
        console.log(origin);
        const response = await axios.get(`${origin}/dashboard-velocity`);
        this.dataVelocity = response.data;
      } catch (error) {
        console.error("Erro ao carregar dados:", error);
      }
    },

    async saveAllVelocity() {
      try {
        let origin = location.origin.replace(/:\d+$/, ":3333");
        for (const item of this.dataVelocity) {
          const { id, title, value: newValue } = item;
          const oldValue = this.getOldValue(id);

          if (oldValue !== newValue) {
            await axios.put(`${origin}/dashboard-velocity/${id}`, {
              value: Number(newValue),
              title,
            });
          }
        }

        this.$refs.successModalVelocity.show();
      } catch (error) {
        console.error("Erro ao salvar dados:", error);
      }
    },
    async loadDataVelocityOld() {
      try {
        let origin = location.origin.replace(/:\d+$/, ":3333");
        const response = await axios.get(`${origin}/dashboard-velocity`);
        this.dataVelocityOld = response.data;
      } catch (error) {
        console.error("Erro ao carregar dados antigos:", error);
      }
    },
    getOldValue(id) {
      const oldItem = this.dataVelocityOld.find((item) => item.id === id);
      return oldItem ? oldItem.value : null;
    },

    async loadBreaks() {
      try {
        let origin = location.origin.replace(/:\d+$/, ":3333");
        const response = await axios.get(`${origin}/breaks`);
        const data = response.data.filter((breakItem) => breakItem.reason !== "Hold");

        const newData = data.map((item) => {
          let timeInSeconds = item.time || 0;
          if (typeof timeInSeconds === "string" && timeInSeconds.length === 8) {
            timeInSeconds = timeInSeconds.slice(0, -2);
          }
          const hours = Math.floor(timeInSeconds / 3600);
          const remainingSeconds = timeInSeconds % 3600;
          const minutes = Math.floor(remainingSeconds / 60);

          const formattedTime = `${String(hours).padStart(2, "0")}:${String(
            minutes
          ).padStart(2, "0")}`;

          return {
            ...item,
            time: formattedTime,
            editedTime: formattedTime,
          };
        });

        this.dataBreaks = newData;
      } catch (error) {
        console.error("Erro ao carregar dados:", error);
      }
    },

    async saveAllBreaks() {
      try {
        let origin = location.origin.replace(/:\d+$/, ":3333");
        for (const breakItem of this.dataBreaks) {
          const { id, time, reason } = breakItem;

          const timeInSeconds = this.timeToSeconds(time);
          await axios.put(`${origin}/breaks/${id}`, {
            reason,
            time: timeInSeconds,
          });
        }
        this.$refs.successModalBreaks.show();
      } catch (error) {
        console.error("Erro ao salvar dados:", error);
      }
    },
    timeToSeconds(timeString) {
      const [hours, minutes] = timeString.split(":").map(Number);
      const totalSeconds = hours * 3600 + minutes * 60;
      return totalSeconds;
    },
  },
};
</script>

<style scoped>
.flex-container {
  display: flex;
  justify-content: space-evenly;
  align-items: center;
  width: 100%;
}

.flex-container > * {
  margin-right: 10px;
}

.center-button {
  margin-top: 10px;
  text-align: center;
}
</style>
